import { StrictMode } from "react";
import { createRoot } from "react-dom/client";

import App from "./App";

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);
const name = "demo";
const fName = "Ketan";
const lName = "Bhoot";
const dateObj = new Date();

const customStyle = {
  color: "red",
  "border-bottom": "1px solid green"
};

customStyle.color = "pink";

const currentTime = dateObj.getHours();
const customStyle2 = {
  color: ""
};
let greetingText = "";
if (currentTime < 12) {
  greetingText = "Very Good Morning";
  customStyle2.color = "pink";
} else if (currentTime < 18) {
  greetingText = "Very Good Afternoon";
  customStyle2.color = "sky";
} else {
  greetingText = "Good Night";
  customStyle2.color = "violet";
}

root.render(
  <div>
    <div className="class">
      <h1 dir="rtl" draggable>
        Hellow World!
      </h1>
      <p>This is a paragraph.</p>
    </div>
    <div>
      <h1>This is my favourite food!</h1>
      <ul>
        <li>Roti Sabji</li>
        <li>Dhosa</li>
        <li>Fry Idli</li>
      </ul>
    </div>
    <div>
      <h1>Your input name is : {name}</h1>
    </div>
    <div>
      <h1>Your input name is : {`${fName} ${lName}`}</h1>
      <h2>Your input name is : {fName + " " + lName}</h2>
    </div>
    <div>
      <h1>Your input name is : {fName + " " + lName}</h1>
      <p>Copyright &copy; {dateObj.getFullYear()}</p>
    </div>
    <div className="image_list">
      <img
        src="https://image.shutterstock.com/image-photo/indian-food-paneer-butter-masala-260nw-1063552880.jpg"
        alt="one"
      />
      <img src="https://picsum.photos/200/300" alt="image2" />
      <img
        src="https://img.onmanorama.com/content/dam/mm/en/food/special-diet/images/2019/7/30/fried-idli.jpg"
        alt="two"
      />
    </div>

    <div>
      <h1 style={customStyle}>Inline Styling</h1>
    </div>

    <div>
      <h1 style={customStyle2}>{greetingText}</h1>
    </div>
  </div>
);
